  font-family: "AvenirNext";
  src: url("./assets/AvenirNext/AvenirNext-Italic.ttf");
  font-style: italic;
}


@font-face {
  font-family: "AvenirNext";
  src: url("./assets/AvenirNext/AvenirNext-Medium.ttf");
